#root > div {
    touch-action: none;
}

.touch-area {
    height: 90vh;
    z-index: 10;
    /* touch-action: pinch-zoom; */
    /* background-color: rgb(203, 240, 255); */
}

.cards {
    /* height: 90%; */
    display: grid;
    /* grid-template-columns: repeat(auto-fill, minmax(30%, 1fr)); */
    grid-gap: 5px;
    max-width: 90%;
    margin: 0 auto 30px;
}

.card-area {
    position: relative;
}

.card-img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

/* 여기서부터 지워야 돼 */
.PhotoGrid__imageGridItem___hFxCm {
    width: 100%;
    height: 80%;
    display: block;
    box-sizing: border-box;
    float: left;
    padding: 10px;
    /* object-fit: cover; */
    overflow: hidden;
    position: relative;
    transition: all 0.2s linear;
    transition-delay: 0.03s;
    background-color: coral;
}
.PhotoGrid__imageWrapper___1YTK9 {
    /* position: relative; */
    width: 100%;
    height: 30px;
    /* object-fit: cover; */
    overflow: hidden;
    /* padding-bottom: 100%; */
    /* background-position: center center;
    background-repeat: no-repeat; */
    transition: all 0.5s linear;
    cursor: pointer;
    background-color: pink;
    /* box-shadow: 0 10px 6px -6px rgba(0, 0, 0, 0.3),
      0 0 40px rgba(0, 0, 0, 0.1) inset; */
}
.PhotoGrid__imageGridItem___hFxCm:hover .PhotoGrid__imageWrapper___1YTK9 {
    background-position: right;
}

.PhotoGrid__imageGridItem___hFxCm a {
    display: none;
    font-size: 100%;
    color: #ffffff !important;
    text-align: center;
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    height: 50px;
    cursor: pointer;
    text-decoration: none;
}

.PhotoGrid__imageGridItem___hFxCm:hover a,
.PhotoGrid__imageGridItem___hFxCm:focus a {
    display: block;
}

.PhotoGrid__imageGridItem___hFxCm:hover .PhotoGrid__imageWrapper___1YTK9:before,
.PhotoGrid__imageGridItem___hFxCm:focus
    .PhotoGrid__imageWrapper___1YTK9:before {
    display: block;
}

.PhotoGrid__imageWrapper___1YTK9:before {
    content: "";
    display: none;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(29, 38, 41, 0.65);
}
.PhotoGrid__column1___3xTvr {
    width: 100%;
    transition: all 0.5s linear;
}
.PhotoGrid__column1Image___2KDMR {
    float: left;
    width: 30%;
    margin-right: 10px;
    padding-bottom: 30%;
    transition: all 0.5s linear;
}

.PhotoGrid__lightbox___2hnqF {
    /** Default lightbox to hidden */
    /*display: block;*/
    opacity: 1;
    /** Position and style */
    position: fixed;
    z-index: 999;
    width: 100%;
    height: 100%;
    text-align: center;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.88);
    visibility: visible;
    /*transition: visibility .3s, opacity .3s linear;*/
    transition: all 0.5s;
}

.PhotoGrid__lightbox___2hnqF img,
.PhotoGrid__lightbox___2hnqF video {
    /** Pad the lightbox image */
    max-width: 100%;
    max-height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
    /*padding: 10px;*/
    -webkit-transition: opacity 1s ease-in-out;
    -moz-transition: opacity 1s ease-in-out;
    -o-transition: opacity 1s ease-in-out;
    transition: opacity 1s ease-in-out;
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    filter: alpha(opacity=0);
    pointer-events: none;
}

.PhotoGrid__lightbox___2hnqF img.opaque,
.PhotoGrid__lightbox___2hnqF video.opaque {
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
    filter: alpha(opacity=1);
    pointer-events: auto;
}

.PhotoGrid__hide___3-4dl {
    /*display:none;*/
    opacity: 0;
    visibility: hidden;
    transition: visibility 0.3s, opacity 0.3s linear;
    /*transition: all .5s;*/
}
.DefaultInfoElement__info___OKuK5 {
    transition: all 5s;
    transition-delay: 2s;
}

.button__in__video {
    position: absolute;
    top: 50%;
    margin: -17% 0 0 -65%;
    width: 35%;
}

